.welcome{
    text-align: center;
    .header{
        background-image: url('../images/welcome-header.png');
        background-position: center top;
        background-repeat: no-repeat;
        color: white;
        padding: 30px;
        .title,img{
            margin-bottom: 10px;
        }
        .subtitle{
            font-family: 'pacifico';
            font-size: 50px;
            font-weight: normal;
        }
        img{
            display: block;
            margin: 0 auto;
            max-width: 100%;
            height: auto;
        }
    }
    .main{
        max-width: 350px;
        margin: 0 auto;
        padding: 25px 15px 15px;
        img{
            border-radius: 20px;
            margin-bottom: 20px;
            width: 100%;
            height: auto;
        }
        .description{
            margin-bottom: 25px;
        }
        button{
            width: 100%;
        }
    }
}
.app-page,.welcome{
    position: fixed;
    z-index: 1030;
    width: 100%;
    min-height: 100vh;
    background-color: white;
}
.index{
    max-width: 750px;
    margin: 0 auto;
    .menu{
        position: fixed;
        display: none;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        //background-color: rgba(16, 16, 16, .5);
        z-index: 1020;
        .menu-flex{
            display: flex;
            height: 100%;
        }
        .menu-inner{
            display: flex;
            flex-direction: column;
            background-color: white;
        }
        .header{
            background-color: $color-blue;
            display: flex;
            justify-content: space-between;
            padding: 15px;
            font-size: 24px;
            color: white;
            button{
                background-color: transparent;
                border: none;
            }
        }
        ul{
            list-style: none;
            padding-inline-start: 0;
            margin: 0;
            li{
                font-size: 20px;
                a{
                    color: $color-text;
                    text-decoration: none;
                    display: flex;
                    align-items: center;
                    padding: 0 20px;
                    i{
                        color: $color-pink;
                        margin-right: 15px;
                        width: 25px;
                        min-width: 25px;
                    }
                    span{
                        border-bottom: 1px solid rgba(209, 209, 209, 1);
                        padding: 20px 0;
                        flex-grow: 1;
                        white-space: nowrap;
                    }
                }
            }
        }
        .footer{
            margin-top: auto;
            font-family: 'sourcesanspro-regular';
            font-size: 16px;
            padding: 20px;
            white-space: nowrap;
            .semibold{
                font-family: 'sourcesanspro-semibold';
            }
        }
    }
    @media (min-width: 768px){
        display: flex;
        .menu{
            position: static;
            display: block;
            flex: 0 0 40%;
            width: 40%;
            padding-top: 20px;
            background-color: transparent;
            z-index: 0;
            .header{
                display: none;
            }
            ul{
                li{
                    background-color: #F8F8F8;
                    overflow: hidden;
                    &.current,&:hover{
                        background-color: #EAEBEC;
                        a{
                            border-left: 3px solid $color-pink;
                            span{
                                color: $color-pink;
                                white-space: normal;
                            }
                        }
                    }
                    &:first-child{
                        border-top-left-radius: 10px;
                        border-top-right-radius: 10px;
                    }
                    &:last-child{
                        border-bottom-left-radius: 10px;
                        border-bottom-right-radius: 10px;
                        a span{
                            border-bottom: none;
                        }
                    }
                }
            }
            .footer{
                display: none;
            }
        }
    }
}
.categories{
    @media (min-width: 768px){
        flex: 0 0 60%;
        width: 60%;
    }
    padding: 20px;
    .category{
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 135px;
        padding: 15px;
        border-radius: 10px;
        text-decoration: none;
        margin-bottom: 20px;
        background-color: $color-blue;
        position: relative;
        overflow: hidden;
        &::before{
            position: absolute;
            width: 190px;
            height: 190px;
            content: '';
            border-radius: 50%;
            right: -90px;
            bottom: -90px;
            background-color: black;
            opacity: .1;
        }
        .label{
            font-size: 24px;
        }
        img{
            max-width: 100px;
            height: auto;
        }
        .label,img{
            position: relative;
        }
    }
}
.tasks-page{
    max-width: 750px;
    margin: 0px auto;
    background-color: #F8F8F8;
    min-height: 90.8vh;
}
.tasks{
    padding: 20px;
    .task{
        display: flex;
        justify-content: space-between;
        border-radius: 10px;
        text-decoration: none;
        margin-bottom: 10px;
        .label{
            flex-grow: 1;
            padding: 15px;
            font-size: 18px;
            border-top-left-radius: 10px;
            border-bottom-left-radius: 10px;
        }
        .arrow{
            padding: 15px;
            border-top-right-radius: 10px;
            border-bottom-right-radius: 10px;
        }
    }
}
.task-detail{
    max-width: 750px;
    margin: 0px auto;
    background-color: #F8F8F8;
    .banner{
        img{
            width: 100%;
            height: auto;
            display: block;
        }
    }
    .details{
        padding: 20px;
        .btn-feedback{
            display: flex;
            align-items: center;
            span{
                text-align: left;
                padding-left: 15px;
            }
        }
        .list{
            .item{
                display: flex;
                align-items: center;
                .right{
                    flex-grow: 1;
                    padding: 20px 0;
                    line-height: 2rem;
                    border-bottom: 1px solid $color-bottom-border;
                }
                .left{
                    //flex: 0 0 20%;
                    //width: 20%;
                    background-color: $color-blue;
                    width: 35px;
                    min-width: 35px;
                    height: 35px;
                    border-radius: 50%;
                    margin-right: 15px;
                    color: white;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                }
            }
        }
    }
}
.lightbox{
    &.product{
        z-index: 1020;
        .dialog{
            background-color: white;
            width: 100%;
            max-height: 100%;
            margin: 1rem auto;
            padding: 20px;
            border-radius: 15px;
            position: relative;
            .close{
                border: none;
                background: none;
                color: $color-pink;
                font-size: 25px;
                position: absolute;
                top: 8px;
                right: 8px;
                padding: 0;
                margin: 0;
                line-height: 25px;
            }
            .header{
                display: flex;
                align-items: center;
                justify-content: left;
                padding: 0 0 10px;
                font-size: 16px;
                .text{
                    overflow: hidden;
                }
                img{
                    height: auto;
                    margin-right: 8px;
                    max-width: 25%;
                }
                h3{
                    font-size: 24px;
                }
                .number{
                    font-size: 16px;
                }
            }
            .body{
                border-top: 1px solid $color-bottom-border;
                .wrapper{
                    display: flex;
                    padding-top: 10px;
                    .icon i{
                        margin-right: 10px;
                    }
                    .description{
                        font-size: 18px;
                    }
                }
            }
        }
    }

    &.materials{
        .dialog{
            background-color: white;
            width: 100%;
            max-height: 100%;
            margin: 1rem auto;
            padding: 20px;
            border-radius: 15px;
            position: relative;
            .close{
                border: none;
                background: none;
                color: $color-pink;
                font-size: 25px;
                position: absolute;
                top: 8px;
                right: 8px;
                padding: 0;
                margin: 0;
                line-height: 25px;
            }
            h3{
                font-size: 24px;
                margin-bottom: 20px;
            }
            .item{
                display: flex;
                align-items: center;
                .image{
                    margin-right: 10px;
                    img{
                        max-width: 50px;
                        height: auto;
                    }
                }
                .right{
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    padding: 20px 0;
                    border-bottom: 1px solid $color-bottom-border;
                    flex-grow:1;
                    .name{
                        font-size: 18px;
                    }
                    .check{
                        font-size: 30px;
                        background: none;
                        border: none;
                    }
                }
                &:last-child .right{
                    border-bottom: none;
                }
            }
        }
    }

    &.wizard{
        z-index: 1010;

        #wizard-wrapper {
            background-color: $color-pink;
            border-radius: 15px;
            overflow: hidden;
        }
        .dialog{
            width: 100%;
            max-height: 100%;
            margin: 0 auto;
            padding: 20px;
            border-radius: 15px;
            position: relative;
            transform: translateX(0);
            transition: transform .3s;
            .close{
                border: none;
                background: none;
                color: white;
                font-size: 25px;
                position: absolute;
                top: 8px;
                right: 8px;
                padding: 0;
                margin: 0;
                line-height: 25px;
            }
            .header{
                background-color: white;
                border-radius: 10px;
                text-align: center;
                font-size: 18px;
                margin: 20px 0 15px;
                padding: 15px;
                display: block;
                position: relative;
                .logo{
                    position: absolute;
                    top: -30px;
                    left: 15px;
                    border-radius: 50%;
                    background-color: $color-blue;
                    padding: 15px;
                    img{
                        display: block;
                    }
                }
                .title{
                    margin-bottom: 10px;
                    font-size: 18px;
                }
                .pacifico{
                    font-family: 'pacifico';
                    font-weight: normal;
                }
                .description{
                    font-family: 'sourcesanspro-regular';
                    margin-bottom: 10px;
                }
                .button button{
                    background: none;
                    border: 1px solid $color-pink;
                    border-radius: 5px;
                    font-size: 16px;
                    font-family: 'sourcesanspro-regular';
                    color: $color-pink;
                    padding: 5px 10px;
                    &:hover{
                        background: $color-pink;
                        border-color: white;
                        color: white;
                    }
                }
            }
            .image{
                margin-bottom:10px;
                img{
                width: 100%;
                height: auto;
                border-radius: 10px;
                }
            }
            .nav{
                display: flex;
                justify-content: space-between;
                align-items: center;
                padding-bottom: 15px;
                button{
                    background-color: $color-pink-btn;
                    border: none;
                    border-radius: 10px;
                    color: white;
                    padding: 10px 20px;
                }
                .dots{
                    display: flex;
                    justify-content: center;
                    .dot{
                        width: 7px;
                        height: 7px;
                        border-radius: 50%;
                        background-color: white;
                        margin: 3px;
                        &:not(.current){
                            opacity: .35;
                        }
                    }
                }
            }
            .links{
                display: flex;
                flex-wrap: nowrap;
                padding-top: 20px;
                margin: 0 -3px;
                border-top: 1px solid rgba(255,255,255,.5);
                a{
                    flex-grow: 1;
                    background-color: white;
                    color: $color-pink;
                    text-align: center;
                    border-radius: 10px;
                    font-size: 14px;
                    text-decoration: none;
                    padding: 10px;
                    margin: 0 3px;
                    border: 1px solid white;
                    &:hover{
                        color: white;
                        background-color: $color-pink;
                    }
                }
            }
            &.right{
                transform: translateX(150%);
                transition: transform .3s;
            }
            &.left{
                transform: translateX(-150%);
                transition: transform .3s;
            }
        }
        .intro{
            h2,.body,.footer{
                text-align: center;
                font-family: 'sourcesanspro-regular';
            }
            h2,.footer{
                font-family: 'pacifico';
                font-weight: normal;
                font-size: 18px;
            }
            h2{
                padding: 16px 0;
            }
            .tilde{
                img{
                    display: block;
                    margin:0 auto;
                }
            }
        }
    }

    &.feedback{
        .dialog{
            background-color: #fff;
            width: 100%;
            max-height: 100%;
            margin: 1rem auto;
            padding: 20px;
            border-radius: 15px;
            position: relative;
            text-align: center;
            &>*,input[type="submit"]{
                margin-top: 10px;
            }
            .close{
                border: none;
                background: none;
                color: $color-pink;
                font-size: 25px;
                position: absolute;
                top: 8px;
                right: 8px;
                padding: 0;
                margin: 0;
                line-height: 25px;
            }
            .title{
                font-size: 24px;
                color: $color-blue;
            }
            .description{
                font-size: 18px;
                line-height: 24px;
                font-family: 'sourcesanspro-regular';
            }
            .stars{
                color: #FFB941;
                font-size: 30px;
                display: flex;
                justify-content: center;
                border-top: 1px solid $color-bottom-border;
                padding: 20px 0;
                label{
                    margin-right: 5px;
                    &:last-of-type{
                        margin-right: 0;
                    }
                }
                input[type="radio"],label:first-of-type{
                    display: none;
                    &:checked{
                        &+label~label{
                        color: #D6D6D6;
                    }}
                }
            }
            textarea{
                background-color: #F9F9F9;
                border: 1px solid #EAEEF2;
                border-radius: 10px;
                width: 100%;
                height: 110px;
                padding: 15px;
                font-size: 18px;
                font-family: 'sourcesanspro-regular';
            }
            .review-invalid{
                color: $color-pink;
                font-size: 14px;
                font-family: 'sourcesanspro-regular';
                display: none;
            }
        }
    }
}

.products{
    padding: 20px;
    max-width: 750px;
    margin: 0px auto;
    .product{
        display: flex;
        align-items: center;
        text-decoration: none;
        color: $color-text;
        background-color: #EAEEF2;
        border-radius: 10px;
        margin-bottom: 10px;
        overflow: hidden;
        padding: 10px;
        .image{
            flex: 0 0 20%;
            width: 20%;
            @include media-query('tablet','min'){
                flex: 0 0 10%;
                width: 10%;
            }
            margin-right: 10px;
            img{
                max-width: 50px;
                width: 100%;
                height: auto;
                display: block;
            }
        }
        h3{
            font-family: 'sourcesanspro-regular';
            font-weight: normal;
            font-size: 18px;
            overflow: hidden;
        }
        i{
            margin-left: auto;
        }
    }
}
.product-slider{
    max-width: 750px;
    margin: 0px auto;
    .item{
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #EAEEF2;
        img{
            width: initial;
        }
    }
}
.product-detail{
    padding: 20px;
    max-width: 750px;
    margin: 0px auto;
    h2{
        margin-bottom: 10px;
    }
    h3{
        margin: 30px 0 24px;
    }
    &>.description{
        font-size: 18px;
    }
    .accordion .message{
        padding: 0 15px;
        overflow: hidden;
        max-height: 200px;
        transition: max-height .3s linear;
        &.closed{
            max-height: 24px;
            transition: max-height .3s linear;
            //white-space: nowrap;
            //text-overflow: ellipsis;

        }
    }
    .alternative{
        .product{
            display: flex;
            align-items: center;
            margin-bottom: 10px;
            .image{
                flex: 0 0 25%;
                width: 25%;
                background-color: #EAEEF2;
                border-radius: 10px;
                margin-right: 15px;
                overflow: hidden;
                img{
                    width: 100%;
                    height: auto;
                    display: block;
                }
            }
            h4{
                font-size: 18px;
            }
        }
    }
}

.contact {
    padding: 20px 40px;
    max-width: 750px;
    margin: 0px auto;

    .input-group {

        margin-bottom: 25px;

        label.label {
            display: block;
            margin-bottom: 15px;
        }

        .fas {
            color: $color-blue;
            margin-right: 5px;
            font-size: 14px;
        }

        input,
        textarea {
            padding: 10px;
            border-radius: 10px;
            border: 1px solid #E8E8E8;
            background-color: #F9F9F9;
            display: block;
            width: 100%;
        }

        textarea {
            min-height: 100px;
        }

        input::-webkit-input-placeholder,
        textarea::-webkit-input-placeholder {
          color: #A0A6AA;
        }
    }

    button.btn-contact {
        text-align: left;
        margin-top: 30px;
        .fas {
            float: right;
        }
    }
}

.lightbox.contact {

    .dialog {
        max-width: 550px;
        background-color: #fff;
        width: 100%;
        max-height: 100%;
        margin: 1rem auto;
        padding: 20px;
        border-radius: 15px;
        position: relative;
        text-align: center;

        .close {
            border: none;
            background: none;
            color: #EB3983;
            font-size: 25px;
            position: absolute;
            top: 8px;
            right: 8px;
            padding: 0;
            margin: 0;
            line-height: 25px;
        }

        .icon {
            font-size: 60px;
            color: $color-blue;
            margin-top: 10px;
        }

        .title {
            font-size: 24px;
            color: $color-blue;
            margin-bottom: 20px;
        }

        .content {
            font-size: 18px;
            line-height: 24px;
            font-family: 'sourcesanspro-regular';
            margin-bottom: 30px;
        }
    }
}

#tips-lightbox {

    .dialog-wrapper {
        position: relative;
    }

    button.close {
        border: none;
        background: none;
        color: white;
        font-size: 25px;
        position: absolute;
        top: 8px;
        right: 8px;
        padding: 0;
        margin: 0;
        line-height: 25px;
        z-index: 1;
    }

    .dialog {
        background-color: #EB3983;
        width: 100%;
        max-height: 100%;
        margin: 0 auto;
        padding: 20px;
        border-radius: 15px;
        position: relative;
        transform: translateX(0);
        transition: transform .3s;
        color: white;
        max-height: 80vh;
        overflow: auto;

        .title {
            font-family: 'pacifico';
            font-weight: normal;
            font-size: 24px;
            text-align: center;
        }

        .title+img {
            display: block;
            margin: 20px auto;
        }

        .tips-list {
            font-family: 'sourcesanspro-regular';

            .item {
                border-bottom: 1px solid rgba(255,255,255,0.3);
                padding: 20px 0;

                &:last-child {
                    border-bottom: none;
                }
            }

            .number {
                position: absolute;
                background-color: white;
                color: #eb3983;
                padding: 5px 12px;
                border-radius: 100px;
                font-family: 'sourcesanspro-semibold';
            }

            .content {
                padding-left: 45px;
            }
        }
    }
}
