body{
    font-family: 'sourcesanspro-semibold';
    color: $color-text;
}

*{
    box-sizing: border-box;
}

h1,h2,h3,h4,h5,h6{
    margin: 0;
}

.bg-blue{
    background-color: $color-blue;
}

.bg-pink{
    background-color: $color-pink;
}

.bg-pink-2{
    background-color: $color-pink-2;
}

.bg-blue-2{
    background-color: $color-blue-2;
}

.text-white{
    color: white;
}

.text-blue{
    color: $color-blue;
}

.text-pink{
    color: $color-pink;
}

.font-bold{
    font-weight: bold;
}

.font-normal{
    font-family: 'sourcesanspro-regular';
}

.owl-carousel,.owl-theme{
    position: relative;
    .item img{
        height: auto;
    }
    .owl-nav{
        position: absolute;
        top: 50%;
        left: 0;
        right: 0;
        transform: translateY(-50%);
        display: flex;
        justify-content: space-between;
        font-size: 14px;
        button{
                &.owl-prev,&.owl-next{
                background-color: rgba(4, 68, 136, .5);
                margin:0;
                height: 70px;
                width: 35px;
                border-radius: 0;
            }
            &.owl-next{
                border-bottom-left-radius: 70px;
                border-top-left-radius: 70px;
            }
            &.owl-prev{
                border-bottom-right-radius: 70px;
                border-top-right-radius: 70px;
            }
        }
    }
    .owl-dots{
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        .owl-dot{
            span{
                background: $color-blue;
                height: 7px;
                width: 7px;
                margin: 2.5px;
            }
            &:hover span,&.active span{
                background: $color-pink;
            }
        }
    }
}

.main {
    padding-top: 60px;
}

.floating{
    position: fixed;
    bottom: 10px;
    right: 10px;
    z-index: 1000;
    button{
        padding: 20px;
        border: 0;
        border-radius: 50%;
        box-shadow: 0 5px 25px rgba(235, 57, 131, 0.5);
        overflow: hidden;
        img{
            display: block;
        }
    }
}

.btn{
    border: 1px solid transparent;
    background-color: transparent;
    padding: 15px;
    border-radius: 10px;
    &-pink{
        background-color: $color-pink;
        color: white;
        border-color: white;
        &:hover{
            background-color: white;
            color: $color-pink;
            border-color: $color-pink;
        }
    }
    &-outline-pink{
        color: $color-pink;
        border: 1px solid $color-pink;
        &:hover{
            background-color: $color-pink;
            color: white;
            border-color: white;
        }
    }
    &-sm{
        padding: 5px;
        line-height: 1rem;
    }
    &-block{
        display: block;
        width: 100%;
    }
}

.lightbox{
    position: fixed;
    background-color: rgba(0,0,0,.5);
    z-index: 1010;
    top: 0;
    left: 0;
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    padding: 17.5px;
    display: flex;
    justify-content: center;
    align-items: center;
    .dialog{
        max-width: 550px;
    }
    &:not(.hide){
        visibility: visible;
        opacity: 1;
        transition: opacity .3s linear;
    }
    &.hide{
        visibility: hidden;
        opacity: 0;
        transition: visibility 0s .3s, opacity .3s linear;
    }
}

.sliding-page{
    display: none;
}

.mejs-container{
    max-width: 100%;
    video{
        max-width: 100%;
    }
}

.accordion{
    background-color: #EAEEF2;
    border-radius: 10px;
    margin-bottom: 10px;
    padding: 16px;
    display: flex;
    font-size: 18px;
    line-height: 24px;
    .arrow{
        margin-left: auto;
    }
}
